/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

//Default Styles

//Theme Default

@import "../../../theme-defaults.scss";

//Color values
$DEFAULT__bs-counter__description--color: $THEME-PRIMARY-COLOR--charoal-grey;
$DEFAULT__bs-counter__learn-more--color: $THEME-PRIMARY-COLOR--cool-grey;
$DEFAULT__bs-counter__title--color: $THEME-PRIMARY-COLOR--black;

//Font Weights
$DEFAULT__bs-counter__font-weight--bold: $THEME-FONT-WEIGHT-BOLD;
$DEFAULT__bs-counter__font-weight--medium: $THEME-FONT-WEIGHT-MEDIUM;
$DEFAULT__bs-counter__font-weight--regular: $THEME-FONT-WEIGHT-REGULAR;
$DEFAULT__bs-counter__font-weight--light: $THEME-FONT-WEIGHT-LIGHT;

//Font Sizes
$DEFAULT__bs-counter__font-size--extra-small: 13px;
$DEFAULT__bs-counter__font-size--small: 16px;
$DEFAULT__bs-counter__font-size--medium: 22px;
$DEFAULT__bs-counter__font-size--large: 24px;

$DEFAULT__bs-counter__description--font-size: $DEFAULT__bs-counter__font-size--small;
$DEFAULT__bs-counter__counter--font-size: $DEFAULT__bs-counter__font-size--small;
$DEFAULT__bs-counter__title--font-size: $DEFAULT__bs-counter__font-size--medium;

//Image values
$DEFAULT__bs-counter__image--height: 24px;
$DEFAULT__bs-counter__image--margin: 0;
$DEFAULT__bs-counter__image-figure--margin: 0;

//Description values
$DEFAULT__bs-counter-description--paragraph-limit: 3;
$DEFAULT__bs-counter-description--line-height: 1.38;

//Title values
$DEFAULT__bs-counter-title--line-height: 1.38;

// Line Height values
$DEFAULT__bs-counter--line-height: 1;

.bs-counter {
  $this: &;

  &.bs-counter---default {
    #{$this}__container {
      @include clearfix;
    }

    #{$this}__title {
      float: left;
      width: 100%;
      font-size: $DEFAULT__bs-counter__title--font-size;
      font-weight: $DEFAULT__bs-counter__font-weight--medium;
      color: $DEFAULT__bs-counter__title--color;
      line-height: $DEFAULT__bs-counter-title--line-height;
    }

    #{$this}__count__wrap {
      float: left;
    }

    #{$this}__wrapper {
      clear: both;
      float: left;
      margin-bottom: 20px;
      line-height: $DEFAULT__bs-counter--line-height;
    }

    #{$this}__count,
    #{$this}__unit {
      float: left;
      text-align: center;
      span {
        font-weight: $DEFAULT__bs-counter__font-weight--regular;
        font-size: $DEFAULT__bs-counter__font-size--large;
        color: $DEFAULT__bs-counter__learn-more--color;
      }
    }

    #{$this}__prefix,
    #{$this}__postfix {
      text-align: center;
      float: left;
      margin: 0 5px;
      line-height: $DEFAULT__bs-counter--line-height;
      > p {
        margin-bottom: 0;
        font-size: $DEFAULT__bs-counter__font-size--large;
        font-weight: $DEFAULT__bs-counter__font-weight--medium;
        line-height: $DEFAULT__bs-counter--line-height;
      }

      > img {
        margin: $DEFAULT__bs-counter__image--margin;
        width: auto;
        height: $DEFAULT__bs-counter__image--height;
        max-height: $DEFAULT__bs-counter__image--height;
      }
    }

    #{$this}__summary {
      float: left;
      width: 100%;
      font-weight: $DEFAULT__bs-counter__font-weight--regular;
      font-size: $DEFAULT__bs-counter__description--font-size;
      color: $DEFAULT__bs-counter__description--color;
      line-height: $DEFAULT__bs-counter-description--line-height;
      display: -webkit-box;
      -webkit-line-clamp: $DEFAULT__bs-counter-description--paragraph-limit;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}

