//Theme Default

@import "../../../theme-defaults.scss";

//Text Colors
$DEFAULT__bs-blurb-text-primary--color: $THEME-PRIMARY-COLOR--black;
$DEFAULT__bs-blurb-link-primary--color: $THEME-PRIMARY-COLOR--black;

//Font sizes
$DEFAULT__bs-blurb__description--font-size: 16px;
$DEFAULT__bs-blurb__read-more--read-size: 16px;

// Linehights
$DEFAULT__bs-blurb__description--line-height: 22px;
$DEFAULT__bs-blurb__title--line-height: 22px;
$DEFAULT__bs-blurb__subtitle--line-height: 22px;

//Links
$DEFAULT__bs-blurb__read-more--hover: 0.7;

//Margins
$DEFAULT__bs-blurb__title-margin: 0 0 15px 0;
$DEFAULT__bs-blurb__subtitle-margin: 0 0 15px 0;
$DEFAULT__bs-blurb-image--margin: 0 0 20px 0;

.bs-blurb {
  $this: &;
  &.bs-blurb---default {
    #{$this}__image {
      figure {
        margin: $DEFAULT__bs-blurb-image--margin;
        img {
          &.cover {
            object-fit: cover;
            width: 100%;
          }
          &.contain {
            object-fit: contain;
          }
          &.fill {
            object-fit: none;
          }
          &.hover-image {
            display: none;
          }
        }
      }
    }
    #{$this}__title {
      color: $DEFAULT__bs-blurb-text-primary--color;
      line-height: $DEFAULT__bs-blurb__title--line-height;
      margin: $DEFAULT__bs-blurb__title-margin;
    }
    #{$this}__subtitle {
      color: $DEFAULT__bs-blurb-text-primary--color;
      line-height: $DEFAULT__bs-blurb__subtitle--line-height;
      margin: $DEFAULT__bs-blurb__subtitle-margin;
    }
    #{$this}__description {
      color: $DEFAULT__bs-blurb-text-primary--color;
      font-size: $DEFAULT__bs-blurb__description--font-size;
      line-height: $DEFAULT__bs-blurb__description--line-height;
    }
    #{$this}__read-more {
      font-size: $DEFAULT__bs-blurb__read-more--read-size;
      color: $DEFAULT__bs-blurb-link-primary--color;
      &:not(.no-link):hover {
        opacity: $DEFAULT__bs-blurb__read-more--hover;
        text-decoration: none;
      }
    }
  }
}

.bs-blurb {
  $this: &;
  &:hover {
    &.bs-blurb---default {
      #{$this}__image {
        &.hover-option-enabled {
          .hover-image {
            display: block;
          }

          .normal-image {
            display: none;
          }
        }
      }
    }
  }
}
