@import "../../../theme-defaults.scss";

$DEFAULT_bs-tab-slider_tabs--margin: 0 0 40px 0;
$DEFAULT_bs-tab-slider_tabs--text-align: center;

// Increase width of animate line
@-webkit-keyframes increaseWidth {
	0% {
		width: 0;
	}
	100% {
		width: 100%;
	}
}
@keyframes increaseWidth {
	0% {
		width: 0;
	}
	100% {
		width: 100%;
	}
}

.bs-tab-slider {
	&.bs-tab-slider---default {
		.bs-slider {
			&.bs-slider---default {
				&.bs-slider-tabs {
					margin: $DEFAULT_bs-tab-slider_tabs--margin;
					.slick-slide {
						&.slick-current {
							.bs-single-slide {
								position: relative;
								p {
									position: relative;

									&::after {
										content: '';
										width: 100%;
										height: 1px;
										background-color: black;
										position: absolute;
										bottom: 0;
										left: 0;
									}
								}
							}
						}
					}
					.bs-single-slide {
						text-align: $DEFAULT_bs-tab-slider_tabs--text-align;
						p {
							display: inline-block;
							cursor: pointer;
						}
					}
				}
				&.bs-slider-tab-content {
					.bs-single-slide {
						.bs-div {
							height: auto;
							background-position: initial;
						}
					}
				}
			}
		}
		.slick-slider {
			.slick-dots {
				&.slick-custom-dots {
					li {
						width: unset;
						button.slick-custom-dot {
							&:before {
								content: '';
								background-color: transparent;
							}
							width: auto;
							max-width: 100px;
							font-size: $THEME-FONT-SIZE-PARAGRAPH;
							color: $THEME-PRIMARY-COLOR--black;
						}
					}
				}
			}
			.slick-prev {
				&.custom-prev-arrow {
					&:before {
						background-color: transparent;
						background-image: unset;
					}
					width: auto;
					max-width: 100px;
					background-color: transparent;
					font-size: $THEME-FONT-SIZE-PARAGRAPH;
					color: $THEME-PRIMARY-COLOR--black;
				}
			}
			.slick-next {
				&.custom-next-arrow {
					&:before {
						background-color: transparent;
						background-image: unset;
					}
					width: auto;
					max-width: 100px;
					background-color: transparent;
					font-size: $THEME-FONT-SIZE-PARAGRAPH;
					color: $THEME-PRIMARY-COLOR--black;
				}
			}
		}
	}
	&.bs-tab-slider--progress-bar {
		.bs-slider-tabs {
			.slick-slide-title-wrapper {
				position: relative;
				&:before {
					content: '';
					position: absolute;
					display: block;
					top: 0;
					left: 0;
					width: 100%;
					height: 2px;
					background-color: #e7e9ee;
				}
				.slick-slide-animator {
					position: absolute;
					top: 0;
					left: 0;
					width: 0;
					height: 2px;
					background-image: linear-gradient(
						to right,
						#6cc24a 21%,
						#ff6634 100%
					);
					transition: none;
				}
			}
			.slick-current {
				.slick-slide-title-wrapper {
					.slick-slide-animator {
						animation: increaseWidth 5000ms linear forwards;
					}
				}
			}
		}
	}
}
