//Theme Default

@import "../../../theme-defaults.scss";

//Text Colors
$DEFAULT__bs-card-text-primary--color: $THEME-PRIMARY-COLOR--black;
$DEFAULT__bs-card-link-primary--color: $THEME-PRIMARY-COLOR--black;

// Line Heights
$DEFAULT__bs-card__description--line-height: 22px;
$DEFAULT__bs-card__title--line-height: 22px;

//Links
$DEFAULT__bs-card__read-more--hover: 0.7;

//Margins
$DEFAULT__bs-card__title-margin: 0 0 15px 0;
$DEFAULT__bs-card-image--margin: 0 0 20px 0;

// Spacing between left and right containers
$DEFAULT__bs-card__container-left-padding: 15px;
$DEFAULT__bs-card__container-right-padding: 15px;

.bs-card {
  $this: &;
  &.bs-card---default {
    #{$this}__image {
      figure {
        margin: $DEFAULT__bs-card-image--margin;
        img {
          &.cover {
            object-fit: cover;
          }
          &.contain {
            object-fit: contain;
          }
          &.scale-down {
            object-fit: scale-down;
          }
        }
      }
    }
    #{$this}__title {
      color: $DEFAULT__bs-card-text-primary--color;
      line-height: $DEFAULT__bs-card__title--line-height;
      margin: $DEFAULT__bs-card__title-margin;
    }
    #{$this}__description {
      color: $DEFAULT__bs-card-text-primary--color;
      line-height: $DEFAULT__bs-card__description--line-height;
    }
    #{$this}__container--flex {
      display: flex;
    }
    #{$this}__container-left {
      padding-right: $DEFAULT__bs-card__container-left-padding;
      max-width: 30%;
    }
    #{$this}__container-right {
      padding-left: $DEFAULT__bs-card__container-right-padding;
      width: fit-content;
    }
  }
}

@media (min-width: 1200px) {
  .bs-card {
    $this: &;
    &:hover {
      &.bs-card---default {
        #{$this}__image {
          &.hover-option-enabled {
            .hover-image {
              display: block;
            }
            .normal-image {
              display: none;
            }
          }
        }
      }
    }
  }
}
