//Color Attributes

$DEFAULT__bs-posts__post-category--color: $THEME-PRIMARY-COLOR--black;
$DEFAULT__bs-posts__post-description--color: $THEME-PRIMARY-COLOR--charoal-grey;
$DEFAULT__bs-posts__post-learn-more--color: $THEME-PRIMARY-COLOR--mid-green;
$DEFAULT__bs-posts__post-date--color: $THEME-PRIMARY-COLOR--battleship-grey;
$DEFAULT__bs-posts__post-title--color: $THEME-PRIMARY-COLOR--black;

$DEFAULT__bs-posts__post-featured-text--color: $THEME-PRIMARY-COLOR--white;

$DEFAULT__bs-posts__post-slider-pager-default: $THEME-PRIMARY-COLOR--grey;
$DEFAULT__bs-posts__post-slider-pager-active: $THEME-PRIMARY-COLOR--black;

//Font Weights

$DEFAULT__bs-posts__font-weight--bold: $THEME-FONT-WEIGHT-BOLD;
$DEFAULT__bs-posts__font-weight--medium: $THEME-FONT-WEIGHT-MEDIUM;
$DEFAULT__bs-posts__font-weight--regular: $THEME-FONT-WEIGHT-REGULAR;
$DEFAULT__bs-posts__font-weight--light: $THEME-FONT-WEIGHT-LIGHT;

//Font Sizes

$DEFAULT__bs-posts__font-size--extra-small: 13px;
$DEFAULT__bs-posts__font-size--small: 16px;
$DEFAULT__bs-posts__font-size--medium: 22px;

$DEFAULT__bs-posts__post-category--font-size: $DEFAULT__bs-posts__font-size--extra-small;
$DEFAULT__bs-posts__post-description--font-size: $DEFAULT__bs-posts__font-size--small;
$DEFAULT__bs-posts__post-learn-more--font-size: $DEFAULT__bs-posts__font-size--small;
$DEFAULT__bs-posts__post-date--font-size: $DEFAULT__bs-posts__font-size--extra-small;
$DEFAULT__bs-posts__post-title--font-size: $DEFAULT__bs-posts__font-size--medium;

//Alignments

$DEFAULT__bs-posts__post--margin: 0 0 30px 0;
$DEFAULT__bs-posts__post-title--margin: 50px 0;
$DEFAULT__bs-posts__post-category--margin: 0 5px 12px 0;
$DEFAULT__bs-posts__post-category--padding: 0 20px 0 0;
$DEFAULT__bs-posts__post-date--margin: 0 0 8px 0;

$DEFAULT__bs-posts__post-inner-padding: 20px;
$DEFAULT__bs-posts__pagination-margin: 20px 0;

//Other

$DEFAULT__bs-posts__post-description--paragraph-limit: 3;

// ----------- Responsive ----------- //

//Mobile

// Image Attributes

$DEFAULT__bs-posts__post-image-mobile--height: 160px;
$DEFAULT__bs-posts__post-featured-image-mobile--height: 160px;

// Ipad

$DEFAULT__bs-posts__post-featured-image-ipad--height: 300px;

// Image Attributes

//Desktop

// Image Attributes

$DEFAULT__bs-posts__post-image--height: 200px;
$DEFAULT__bs-posts__post-featured-image--height: 420px;

.bs-posts {
  $this: &;

  &.bs-post-block---default {
    #{$this}__normal-title {
      margin: $DEFAULT__bs-posts__post-title--margin;
      text-align: center;
      h3 {
        font-size: $DEFAULT__bs-posts__font-size--medium;
      }
    }
    .bs-post {
      margin: $DEFAULT__bs-posts__post--margin;

      &__category {
        display: inline-block;
        margin: $DEFAULT__bs-posts__post-category--margin;
        padding: $DEFAULT__bs-posts__post-category--padding;
        position: relative;
        &::after {
          content: "/";
          position: absolute;
          right: 0;
          color: $THEME-PRIMARY-COLOR--cool-grey;
          width: 7px;
          height: 13px;
          opacity: 0.4;
          font-size: $DEFAULT__bs-posts__font-size--extra-small;
          top: 3px;
        }
        span {
          font-size: $DEFAULT__bs-posts__post-category--font-size;
          font-weight: $DEFAULT__bs-posts__font-weight--medium;
          color: $DEFAULT__bs-posts__post-category--color;
          text-transform: uppercase;
          &:not(:last-child) {
            display: inline-block;
            padding-right: 5px;
          }
        }
      }
      &__date {
        display: inline-block;
        margin: $DEFAULT__bs-posts__post-date--margin;
        span {
          font-size: $DEFAULT__bs-posts__post-date--font-size;
          font-weight: $DEFAULT__bs-posts__font-weight--regular;
          color: $DEFAULT__bs-posts__post-date--color;
        }
      }
      &__title * {
        font-size: $DEFAULT__bs-posts__post-title--font-size;
        font-weight: $DEFAULT__bs-posts__font-weight--medium;
        color: $DEFAULT__bs-posts__post-title--color;
        line-height: 1.3;
      }
      &__description {
        p {
          font-weight: $DEFAULT__bs-posts__font-weight--regular;
          font-size: $DEFAULT__bs-posts__post-description--font-size;
          color: $DEFAULT__bs-posts__post-description--color;
          line-height: 1.38;
          display: -webkit-box;
          -webkit-line-clamp: $DEFAULT__bs-posts__post-description--paragraph-limit;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      }
      &__learn-more {
        .btn {
          font-weight: $DEFAULT__bs-posts__font-weight--regular;
          font-size: $DEFAULT__bs-posts__post-learn-more--font-size;
          color: $DEFAULT__bs-posts__post-learn-more--color;
          &:hover {
            opacity: 0.7;
          }
        }
      }
      &__image {
        figure {
          width: 100%;
          img {
            width: 100%;
            height: $DEFAULT__bs-posts__post-image--height;
            object-fit: cover;
          }
        }
      }
      &__container {
        position: relative;
        > a {
          display: block;
          width: 100%;
          height: 100%;
        }
        &.bs-bg-image {
          height: 100%;
          min-height: 300px;
          background-repeat: no-repeat;
          background-size: cover;
          background-position: center center;
          &:after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            background-image: linear-gradient(
              to bottom,
              $THEME-PRIMARY-COLOR--black 23%,
              rgba(0, 0, 0, 0.31)
            );
            top: 0;
            opacity: 0.86;
          }
          .bs-post {
            &__inner {
              height: 100%;
            }
            &__details {
              height: 100%;
              position: relative;
              padding: $DEFAULT__bs-posts__post-inner-padding;
              z-index: 1;
              * {
                color: $DEFAULT__bs-posts__post-featured-text--color;
              }
            }
          }
        }
      }
      &-loading {
        display: flex;
        align-items: center;
        justify-content: center;
        > * {
          margin: 25px;
        }
      }
    }
    #{$this}__featured {
      .bs-post {
        &__image {
          figure {
            display: block;
            img {
              width: 100%;
              height: $DEFAULT__bs-posts__post-featured-image-mobile--height;
              object-fit: cover;
            }
          }
        }
      }
      .bs-slick-slider__container {
        .bs-post {
          padding: 0;
          margin: 0;
        }
        .slick-list {
          margin-bottom: 75px;
        }
      }
    }
    .bs-posts {
      &__pagination {
        margin: $DEFAULT__bs-posts__pagination-margin;
        width: 100%;
        .pagination {
          list-style: none;
          padding: 0;
          margin: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-wrap: wrap;
          li {
            margin: 5px;
            width: 40px;
            height: 40px;
            border: 1px solid;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
            &:first-child,
            &:last-child {
              width: auto;
              min-width: 40px;
              min-height: 40px;
              > * {
                &:after {
                  content: "";
                  margin: auto;
                  width: 10px;
                  height: 10px;
                  border-width: 2px 2px 0 0;
                  position: absolute;
                  top: 0;
                  bottom: 0;
                  border-style: solid;
                  visibility: hidden;
                }
              }
            }
            &:first-child {
              > * {
                &:after {
                  transform: rotate(-135deg);
                  left: 15px;
                }
              }
            }
            &:last-child {
              > * {
                &:after {
                  transform: rotate(45deg);
                  right: 15px;
                }
              }
            }
            > * {
              padding: 12px 10px 8px 10px;
              display: flex;
              width: 100%;
              height: 100%;
              justify-content: center;
              align-items: center;
              position: relative;
              line-height: 1.1;
              &:hover {
                text-decoration: none;
              }
            }
          }
        }
      }
      &__load-more {
        margin: 25px auto;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        > * {
          font-size: $DEFAULT__bs-posts__font-size--medium;
          &:hover {
            text-decoration: none;
          }
        }
      }
      &__filters {
        margin: 25px 0;
      }
      &__filters--fields {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-end;
        justify-content: space-between;
        form {
          width: 100%;
          .form-group {
            width: 100%;
          }
          .filter-wrapper {
            display: flex;
            flex-wrap: wrap;
            align-items: flex-end;
            width: 100%;
          }
        }
        input,
        button,
        select {
          -o-appearance: none;
          -ms-appearance: none;
          -webkit-appearance: none;
          -moz-appearance: none;
          appearance: none;
          border: 1px solid;
          background: transparent;
        }
        select::-ms-expand {
          display: none;
        }
        input:-webkit-autofill,
        input:-webkit-autofill:hover,
        input:-webkit-autofill:focus,
        input:-webkit-autofill:active {
          -webkit-box-shadow: 0 0 0 1000px transparent inset;
          -webkit-text-fill-color: inherit;
          transition: background-color 5000s ease-in-out 0s;
        }
        .filter-submit {
          margin-bottom: 15px;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          .btn {
            min-height: 48px;
            min-width: 100px;
          }
        }
        .form-group {
          margin-bottom: 15px;
          width: 100%;
          display: flex;
          flex-wrap: wrap;
          &.bs-posts__filter--tax {
            width: 100%;
          }
          .input-wrapper,
          .select-wrapper {
            width: 100%;
            position: relative;
            > * {
              width: 100% !important;
            }
            .select2-selection,
            select,
            input {
              padding: 10px 50px 10px 15px;
              margin: 0;
              display: flex;
              border-color: inherit;
              border-radius: 0;
            }
          }
          .multi-filter:not(.select2-hidden-accessible) {
            visibility: hidden;
            max-height: 48px;
          }
          .select2-selection,
          .select2-container {
            cursor: pointer;
            min-height: 48px;
          }
          .select2-selection__rendered {
            margin-top: 2px;
            display: flex;
            align-items: center;
          }
          .select2-selection__choice {
            display: none;
          }
          .input-wrapper {
            input {
              padding: 10px 50px 10px 15px;
              margin: 0;
            }
            &:before,
            &::after {
              content: "";
              position: absolute;
              pointer-events: none;
            }
            &:before {
              top: 10px;
              right: 20px;
              width: 20px;
              height: 20px;
              background-color: transparent;
              border: 2px solid;
              border-radius: 100%;
            }
            &:after {
              top: 31px;
              right: 13px;
              width: 12px;
              height: 0;
              border: 1px solid;
              transform: rotate(45deg);
            }
          }
          .select-wrapper {
            select {
              cursor: pointer;
            }
            &:after {
              content: "";
              position: absolute;
              width: 10px;
              height: 10px;
              margin: auto;
              border-width: 2px 2px 0 0;
              border-style: solid;
              -webkit-transform: rotate(135deg);
              -ms-transform: rotate(135deg);
              transform: rotate(135deg);
              right: 25px;
              top: -2px;
              bottom: 0;
              pointer-events: none;
            }
          }
          label {
            margin: 0 10px 0 0;
            width: 100%;
          }
        }
      }
      &__not-found {
        text-align: center;
      }
    }
    .selected-filters {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      ul {
        padding: 0;
        margin: 0;
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        li {
          padding: 7px 15px 5px;
          margin: 5px 10px 5px 0;
          display: flex;
          background: $THEME-PRIMARY-COLOR--charoal-grey;
          > span {
            margin: 0 0 0 18px;
            font-size: 20px;
            line-height: 1.2;
            order: 1;
            cursor: pointer;
          }
          &::marker {
            display: none;
          }
        }
      }
      > span {
        margin: 6px 0 5px;
        line-height: 1;
        a {
          &:hover {
            text-decoration: none;
          }
        }
      }
    }
    .slick-dots {
      li {
        button {
          cursor: pointer;
          background: $DEFAULT__bs-posts__post-slider-pager-default;
          &:before {
            display: none;
          }
        }
        &.slick-active {
          pointer-events: none;
          button {
            background: $DEFAULT__bs-posts__post-slider-pager-active;
          }
        }
      }
    }
    .slick-arrow {
      width: 50px;
      height: 50px;
      top: auto;
      bottom: -75px;
      font-size: 0;
      &.disabled {
        opacity: 0.7;
      }
      &.slick-prev {
        left: auto;
        right: 60px;
        &:after {
          left: 20px;
          transform: rotate(-135deg);
        }
      }
      &.slick-next {
        left: auto;
        right: 0;
        &:after {
          left: 15px;
          transform: rotate(45deg);
        }
      }
      &:before {
        display: none;
      }
      &:after {
        content: "";
        position: absolute;
        width: 15px;
        height: 15px;
        margin: auto;
        border-width: 2px 2px 0 0;
        border-style: solid;
        border-color: $DEFAULT__bs-posts__post-slider-pager-active;
        -webkit-transform: rotate(135deg);
        -ms-transform: rotate(135deg);
        transform: rotate(135deg);
        top: 0;
        bottom: 0;
        pointer-events: none;
      }
    }
    .js-post-blocks-filters {
      margin: 10px auto 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      > * {
        margin: 5px 10px;
        &:hover {
          opacity: 0.7;
          text-decoration: none;
        }
      }
      .is-active {
        pointer-events: none;
        opacity: 0.7;
      }
    }
    .bs-posts__filter--tax {
      border-top: 1px solid;
      border-bottom: 1px solid;
    }
    .filter-expand {
      padding: 15px 15px 13px 15px;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      .expand {
        width: 16px;
        height: 16px;
        position: absolute;
        right: 15px;
        top: 0;
        bottom: 0;
        margin: auto;
        a {
          display: block;
          width: 100%;
          height: 100%;
          position: relative;
          &:before,
          &::after {
            content: "";
            margin: auto;
            display: block;
            width: 100%;
            height: 1px;
            border: 1px solid;
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            transition: all 0.2s ease-in-out;
          }
          &:after {
            transform: rotate(90deg);
          }
        }
      }
      &.active {
        .expand {
          a {
            &:after {
              transform: rotate(0deg);
            }
          }
        }
        + .filter-wrapper {
          height: 100%;
        }
      }
      + .filter-wrapper {
        padding: 0 15px;
        height: 0;
        overflow: hidden;
        transition: all 0.4s ease-in-out;
      }
    }
  }
}
