@import "../../../theme-defaults.scss";

.bs-slider-accordion {
  &.bs-slider-accordion--progress-bar {
    .card {
      position: relative;
      &:before {
        content: "";
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #e7e9ee;
      }
      .slick-slide-animator {
        position: absolute;
        top: 0;
        left: 0;
        width: 0;
        height: 2px;
        background-image: linear-gradient(to right, #6cc24a 21%, #ff6634 100%);
        transition: none;
      }
    }
    .card {
      &.bs-active {
        .slick-slide-animator {
          animation: increaseWidth 5000ms linear forwards;
        }
      }
    }
  }
}
