//Theme Default

@import "../../../theme-defaults.scss";
@import "common";

.select2-dropdown {
  border-color: inherit;
  border-radius: 0;
  background: #fff;
  color: inherit;
}
.select2-results__option {
  position: relative;
  padding-left: 40px;
  &[aria-selected="true"] {
    &:after {
      content: "";
      margin: auto;
      width: 12px;
      height: 6px;
      border-width: 2px 2px 0 0;
      border-style: solid;
      position: absolute;
      top: -2px;
      bottom: 0;
      left: 13px;
      transform: rotate(125deg);
    }
  }
  &:before {
    content: "";
    margin: auto;
    width: 18px;
    height: 18px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 10px;
    border-width: 1px;
    border-style: solid;
  }
}
.fancybox-content {
  &.bs-post {
    &__target--popup {
      height: auto !important;
      max-height: 80%;
      &::-webkit-scrollbar {
        width: 5px;
      }

      &::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px
          $DEFAULT__bs-posts__post-slider-pager-default;
        border-radius: 5px;
      }

      &::-webkit-scrollbar-thumb {
        border-radius: 5px;
        -webkit-box-shadow: inset 0 0 6px
          $DEFAULT__bs-posts__post-slider-pager-active;
      }
    }
  }
}

@include media-breakpoint-up(sm) {
  .bs-posts {
    $this: &;
    &.bs-post-block---default {
      #{$this}__featured {
        .only-featured {
          .bs-post {
            &__inner {
              position: relative;
              display: flex;
              justify-content: space-between;
              > * {
                width: 48%;
              }
            }
            &__image {
              margin-left: 4%;
              order: 1;
              figure {
                margin: 0;
                height: 100%;
                img {
                  height: 100%;
                }
              }
            }
            &__details {
              display: flex;
              flex-wrap: wrap;
              flex-flow: column;
              justify-content: center;
            }
            &__container {
              &.bs-bg-image {
                .bs-post__inner > * {
                  width: 100%;
                }
              }
            }
          }
        }
      }
    }
    &.list-view {
      .bs-post {
        &__inner {
          display: flex;
          align-items: center;
        }
        &__image {
          width: 20%;
          margin-right: 30px;
          .figure {
            margin-bottom: 0;
          }
          + .bs-post__details {
            width: calc(80% - 30px);
          }
        }
      }
    }
  }
}

@include media-breakpoint-up(md) {
  .bs-posts {
    $this: &;
    &.bs-post-block---default {
      .bs-posts {
        &__filters--fields {
          .form-group {
            width: calc(33% - 15px);
            &:last-child {
              margin-right: 0;
            }
          }
          form {
            .filter-wrapper {
              > * {
                width: calc(33% - 15px);
                margin-right: 15px;
                &:last-child {
                  margin-right: 0;
                }
              }
              .filter-submit {
                width: auto;
              }
            }
          }
        }
      }
      #{$this}__featured {
        .bs-slick-slider__container {
          .bs-post {
            &__container {
              &:not(.bs-bg-image) {
                .bs-post {
                  &__inner {
                    position: relative;
                    display: flex;
                    justify-content: space-between;
                    > * {
                      width: 48%;
                    }
                  }
                  &__image {
                    margin-left: 4%;
                    order: 1;
                    figure {
                      margin: 0;
                      height: 100%;
                      img {
                        height: 100%;
                      }
                    }
                  }
                  &__details {
                    display: flex;
                    flex-wrap: wrap;
                    flex-flow: column;
                    justify-content: center;
                  }
                }
              }
            }
          }
          .slick-track {
            display: flex;
            align-items: stretch;
          }
          .slick-slide {
            height: auto;
            > div,
            .bs-post,
            .bs-post__container,
            .bs-post__inner {
              height: 100%;
            }
          }
        }
      }
      .bs-posts__filter--tax {
        border: 0;
      }
      .filter-expand {
        display: none;
        + .filter-wrapper {
          height: 100% !important;
          padding: 0;
        }
      }
    }
  }
  .bs-post {
    &__target--popup {
      .bs-post {
        &__image {
          width: 25%;
          margin-right: 5%;
          float: left;
        }
      }
    }
  }
}

@include media-breakpoint-up(lg) {
  .bs-posts {
    $this: &;
    &.bs-post-block---default {
      .bs-posts {
        &__filters--fields {
          form .form-group,
          .form-group {
            width: calc(25% - 15px);
          }
        }
      }
    }
  }
}

@include media-breakpoint-up(xl) {
  .bs-posts {
    $this: &;
    &.bs-post-block---default {
      .bs-post {
        &__container {
          &.bs-bg-image {
            > a {
              &:hover {
                box-shadow: 0 0 7px 5px $DEFAULT__bs-posts__post-date--color;
              }
            }
          }
        }
      }
      #{$this}__featured {
        .bs-slick-slider__container {
          .bs-post {
            &__container {
              &.bs-bg-image {
                > a {
                  &:hover {
                    box-shadow: none;
                  }
                }
              }
            }
          }
        }
      }
      .slick-dots {
        li {
          button {
            &:hover {
              background: $DEFAULT__bs-posts__post-slider-pager-active;
            }
          }
        }
      }
      .slick-arrow {
        &:hover {
          opacity: 0.7;
        }
      }
    }
  }
}
