//Default Styles

//Theme Default

@import "../../../theme-defaults.scss";

//Color values
$DEFAULT__bs-pro-button__bg--color: $THEME-PRIMARY-COLOR--grey;
$DEFAULT__bs-pro-button__title--color: $THEME-PRIMARY-COLOR--black;
$DEFAULT__bs-pro-button__border--color: $THEME-PRIMARY-COLOR--charoal-grey;

//Font Weights

$DEFAULT__bs-pro-button__font-weight--bold: $THEME-FONT-WEIGHT-BOLD;
$DEFAULT__bs-pro-button__font-weight--medium: $THEME-FONT-WEIGHT-MEDIUM;
$DEFAULT__bs-pro-button__font-weight--regular: $THEME-FONT-WEIGHT-REGULAR;
$DEFAULT__bs-pro-button__font-weight--light: $THEME-FONT-WEIGHT-LIGHT;

//Font Sizes
$DEFAULT__bs-pro-button__font-size--regular: 16px;
$DEFAULT__bs-pro-button__title--font-size: $DEFAULT__bs-pro-button__font-size--regular;

//opacity
$DEFAULT__bs-pro-button--opacity: 0.7;

//Title values
$DEFAULT__bs-pro-button-title--line-height: 1.38;
$DEFAULT__bs-pro-button-title-limit: 3;

//Image values
$DEFAULT__bs-pro-button__image--height: 20.5vw;
$DEFAULT__bs-pro-button__image--max-height: 300px;
$DEFAULT__bs-pro-button__image--margin: 0 0 20px 0;
$DEFAULT__bs-pro-button__image-figure--margin: 0;

.bs-pro-button {
	$this: &;

	&.bs-pro-button---default {
		display: flex;
		border: none;
		background: transparent;

		#{$this}__container {
			display: flex;
			align-items: center;
			margin: 0 30px 18px 0;
			padding: 10px 30px;
			min-width: 140px;
			min-height: 50px;
			border-radius: 4px;
			text-decoration: none;
			line-height: $DEFAULT__bs-pro-button-title--line-height;
			font-weight: $DEFAULT__bs-pro-button__font-weight--regular;
			font-size: $DEFAULT__bs-pro-button__title--font-size;

			font-stretch: normal;
			font-style: normal;
			letter-spacing: normal;
			text-align: center;
		}

		#{$this}__prefix,
		#{$this}__postfix {
		display: inline-block;

			> img {
				margin: $DEFAULT__bs-pro-button__image--margin;
				width: 100%;
			}
		}
	}
}

@include media-breakpoint-up(xl) {
	.bs-pro-button {
		$this: &;

		&.bs-pro-button---default {
			#{$this}__container {
				&:hover {
					opacity: $DEFAULT__bs-pro-button--opacity;
				}
			}
		}
	}
}

@include media-breakpoint-up(sm) {
	.bs-pro-button {
		$this: &;
		.bs-pro-button---default {
			#{$this}__postfix,
			#{$this}__prefix {
				> img {
					margin: $DEFAULT__bs-pro-button__image--margin;
					width: 100%;
					height: $DEFAULT__bs-pro-button__image--height;
					max-height: $DEFAULT__bs-pro-button__image--max-height;
					object-fit: cover;
				}
			}
		}
	}
}
