//Theme Default

@import "../../../theme-defaults.scss";

//Video Styles

$DEFAULT__bs-colum-video-wrapper--height: 100%;
$DEFAULT__bs-colum-video-wrapper--width: 100%;
$DEFAULT__bs-colum-video--height: 100%;
$DEFAULT__bs-colum-video--width: 100%;

.bs-column {
    $this: &;
	&.bs-column---default {
		#{$this}__video-container, 
		#{$this}__image-container {
			position: absolute;
			height: $DEFAULT__bs-colum-video-wrapper--height;
            width: $DEFAULT__bs-colum-video-wrapper--width;
            z-index: -1;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			video, 
			img {
				height: $DEFAULT__bs-colum-video--height;
				width: $DEFAULT__bs-colum-video--width;
				object-fit: cover;
			}
		}
	}
}
