//Theme Default

@import "../../../theme-defaults.scss";

//Text Colors
$DEFAULT__bs-advanced-blurb-text-primary--color: $THEME-PRIMARY-COLOR--black;
$DEFAULT__bs-advanced-blurb-link-primary--color: $THEME-PRIMARY-COLOR--vivd-blue;

//Font sizes
$DEFAULT__bs-advanced-blurb__description--font-size: 16px;
$DEFAULT__bs-advanced-blurb__read-more--read-size: 16px;

// Linehights
$DEFAULT__bs-advanced-blurb__description--line-height: 22px;
$DEFAULT__bs-advanced-blurb__title--line-height: 22px;
$DEFAULT__bs-advanced-blurb__subtitle--line-height: 22px;

//Links
$DEFAULT__bs-advanced-blurb__read-more--hover: 0.7;

//Margins
$DEFAULT__bs-advanced-blurb__title-margin: 0 0 15px 0;
$DEFAULT__bs-advanced-blurb__subtitle-margin: 0 0 15px 0;
$DEFAULT__bs-advanced-blurb-image--margin: 0 0 20px 0;

.bs-advanced-blurb {
  &__image {
    position: relative;

    figure {
      margin: $DEFAULT__bs-advanced-blurb-image--margin;

      img {
        &.object-fit-none {
          object-fit: none;
        }

        &.object-fit-cover {
          object-fit: cover;
          width: 100%;
        }

        &.object-fit-contain {
          object-fit: contain;
        }

        &.object-fit-fill {
          object-fit: fill;
        }
      }
    }

    .hover-image {
      display: none;
    }
  }

  &__title {
    color: $DEFAULT__bs-advanced-blurb-text-primary--color;
    line-height: $DEFAULT__bs-advanced-blurb__title--line-height;
    margin: $DEFAULT__bs-advanced-blurb__title-margin;
  }

  &__subtitle {
    color: $DEFAULT__bs-advanced-blurb-text-primary--color;
    line-height: $DEFAULT__bs-advanced-blurb__subtitle--line-height;
    margin: $DEFAULT__bs-advanced-blurb__subtitle-margin;
  }

  &__description {
    color: $DEFAULT__bs-advanced-blurb-text-primary--color;
    font-size: $DEFAULT__bs-advanced-blurb__description--font-size;
    line-height: $DEFAULT__bs-advanced-blurb__description--line-height;
  }

  &__read-more {
    a {
      color: $DEFAULT__bs-advanced-blurb-link-primary--color;
    }

    span {
      color: $DEFAULT__bs-advanced-blurb-text-primary--color;
    }

    * {
      font-size: $DEFAULT__bs-advanced-blurb__read-more--read-size;
    }
    &:not(.no-link):hover {
      opacity: $DEFAULT__bs-advanced-blurb__read-more--hover;
      text-decoration: none;
    }
  }
}

.bs-advanced-blurb {
  &:hover {
    .hover-option-enabled {
      .hover-image {
        display: block;
      }

      .normal-image {
        display: none;
      }
    }
  }
}
