@import "../../../theme-defaults.scss";

.bs-slider {
  &.bs-slider---default {
    .slick-slider {
      // @include slick-arrows();
      .slick-dots {
        &.slick-custom-dots {
          li {
            width: unset;
            button.slick-custom-dot {
              &:before {
                content: "";
                background-color: transparent;
              }
              width: auto;
              max-width: 100px;
              font-size: $THEME-FONT-SIZE-PARAGRAPH;
              color: $THEME-PRIMARY-COLOR--black;
            }
          }
        }
      }
      .slick-prev {
        &.custom-prev-arrow {
          &:before {
            background-color: transparent;
            background-image: unset;
          }
          width: auto;
          max-width: 100px;
          background-color: transparent;
          font-size: $THEME-FONT-SIZE-PARAGRAPH;
          color: $THEME-PRIMARY-COLOR--black;
        }
      }
      .slick-next {
        &.custom-next-arrow {
          &:before {
            background-color: transparent;
            background-image: unset;
          }
          width: auto;
          max-width: 100px;
          background-color: transparent;
          font-size: $THEME-FONT-SIZE-PARAGRAPH;
          color: $THEME-PRIMARY-COLOR--black;
        }
      }
    }
  }
}
